.login_color {
    color: #e0e0e0;
}

.form_title {
    color: #fc842d;
    font-family: 'Verdana';
    font-size: 14px;
    font-weight: 700;
    text-align: center;
}

.form_grid {
    text-align: center;
}

.login_button {
    width: 182px;
    padding: 1;
    margin: 2;
    height: 44px;
    background-color: #fc842d;
    border-radius: 30px;
    color: #ffffff;
    font-family: 'Verdana';
    font-size: 14px;
    font-weight: 700;
    text-transform: capitalize;
}

.register_button {
    width: 182px;
    background-color: #ffffff;
    height: 44px;
    color: #fc842d;
    border: solid #fc842d;
    border-radius: 30px;
    border-width: 2px;
    font-family: 'Verdana';
    font-size: 14px;
    font-weight: 700;
    text-transform: capitalize;
}

.register_button {
    width: 182px;
}

/* styles for mobile */
@media screen and (min-width: 320px) {
    .form_container {
        padding: 20px;
    }

    .form_container form {
        max-width: 300px;
        margin: 0 auto;
    }

    .form_title {
        padding-bottom: 55px;
        padding-top: 40px;
    }

    .button_container {
        padding: 60px 0px;
    }

    .button_container button {
        margin-bottom: 20px;
    }


}

/* styles for tablet */

@media screen and (min-width: 768px) {
    .form_title {
        text-align: left;
        padding-bottom: 20px;
        padding-top: 176px;
    }

    .form_container {
        padding: 32px;
        display: flex;
        flex-direction: column;
        justify-content: left;

    }

    .form_grid {
        text-align: left;
    }

    .email_input {
        text-align: left;
    }

    .button_container {
        display: inline-flex;
        justify-content: space-between;
        padding-top: 60px;
    }

    .button_container button {
        margin-right: 32px;
    }


}

/* styles for desktop */
@media screen and (min-width: 1280px) {
    .form_container {
        padding: 16px;
    }

    .form_container {
        width: 40%;
    }

}